import React, { useState } from "react";

import { Typography, Box, Button, Grid } from "@material-ui/core";
import useStyles from "../../styles";

import MonthSheet from "../../data/MonthSheet.json";
import DaySheet from "../../data/DaySheet.json";

function CalendarWidget() {
  const classes = useStyles();
  const Today = new Date();

  const [monthIndex, setMonthIndex] = useState(Today.getMonth());
  const [dateIndex, setDateIndex] = useState(Today.getDate());

  return (
    <div>
      <Box className={classes.rowBox} alignItems="center">
        <Typography>{MonthSheet[monthIndex] + " " + dateIndex}</Typography>
        <Box>
          <Button>{"<"}</Button>
          <Button>{">"}</Button>
        </Box>
      </Box>
      <Grid container xs="auto">
        {DaySheet.map(day => (
          <Grid item xs>
            <Box textAlign="center">
              <Typography variant="body2">{day}</Typography>
            </Box>
          </Grid>
        ))}
      </Grid>
      <Grid container xs="auto">
        {DaySheet.map(day => {
          console.log(day);
          return (
            <Grid item xs>
              <Box textAlign="center">
                <Typography variant="body2">{day}</Typography>
              </Box>
            </Grid>
          );
        })}
      </Grid>
      <Grid container xs="auto">
        {DaySheet.map(day => {
          console.log(day);
          return (
            <Grid item xs>
              <Box textAlign="center">
                <Typography variant="body2">{day}</Typography>
              </Box>
            </Grid>
          );
        })}
      </Grid>
      <Grid container xs="auto">
        {DaySheet.map(day => {
          console.log(day);
          return (
            <Grid item xs>
              <Box textAlign="center">
                <Typography variant="body2">{day}</Typography>
              </Box>
            </Grid>
          );
        })}
      </Grid>
      <p>Date List Here</p>
    </div>
  );
}

export default CalendarWidget;
